const uni = require('uni').default;





























export default {
	props:{
		tab:"index"
	},
	methods:{
		goHome:function(){
			 
			uni.navigateBack({
				delta:50,
			})
		},
		goForum:function(){
			uni.navigateTo({
				url:"/pages/forum/index",
			})
		},	
		goAdd:function(){
			uni.navigateTo({
				url:"/pages/forum/add",
			})
		},
		goGroup:function(){
			uni.navigateTo({
				url:"/pages/forum_group/index",
			})
		},	
		goUser:function(){
			if(wx.getStorageSync("authcode")){
				uni.navigateTo({
					url:"/pages/user/index",
				})
			}else{
				uni.navigateTo({
					url:"/pages/login/index",
				})
			}
			
		},	
	}
}



